        <div style="width: 100%;text-align: left;margin-bottom: 10px;">商户名称：{{serviceChargeMsg.name}}</div>
        <div style="width: 100%;text-align: left;margin-bottom: 10px;">收款账号：{{serviceChargeMsg.obj.mbmi_mid}}</div>
        <el-table border class="new-table-fixed-right new-table-fixed" ref="multipleTable" :data="addQueryList"
