          </el-table-column>
          <el-table-column prop="bma_to_device_uuid" label="设备编号">
            <template slot-scope="scope" >
              <span>{{scope.row.bma_to_device_uuid| format}}</span>
            </template>
          </el-table-column>
          <el-table-column label="平台服务费">
            <template slot-scope="scope">
              <div>每笔交易额
