            </template>
          </el-table-column>
          <el-table-column label="银行服务费">
            <template slot-scope="scope">
              <div>每笔交易额
                <el-input style="width: 150px;" :disabled="scope.row.mbmp_partner_uuid&&scope.row.mbpb_fee?false:true"
