        if (this.StateManagementDate.i == 3 && this.textarea == '') {
          this.$message({
            message: '请输入不通过原因',
            type: 'warning'
          });
          return
        }
        let value = this.StateManagementDate.msg
        let index = this.StateManagementDate.i
        let that = this;
        let msg = [{
          bma_uuid: value.bma_uuid,
          bma_mer_uuid: value.mbmi_uuid,
          bma_from_status: "",
          bma_from_describer: "",
          bma_to_status: index,
          bma_to_describer: this.textarea,
          bma_to_bank_center_id: this.customGlobal.userInfoExt.cui_bank_center_id,
          bma_to_bank_province_id: this.customGlobal.userInfoExt.cui_province_id,
          bma_to_bank_city_id: this.customGlobal.userInfoExt.cui_city_id,
          bma_to_bank_county_id: this.customGlobal.userInfoExt.cui_county_id,
          bma_to_bank_station_uuid: this.customGlobal.userInfoExt.cui_station_id,
        }]
        let _jsondata_ = {
          dataList: msg
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.other_affiliate_outlets_modify_status_update,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          if (res.data.status == 'success') {
            loading.close();
            if (this.StateManagementDate.i == 3) {
              this.StateManagement.dialogVisible = false
              this.getData()
              this.$message({
                message: '操作成功',
                type: 'success'
              });
              return
            }
            this.Select_shop()
            that.StateManagement.tableData = res.data.rows;
            that.StateManagement.total = res.data.total
          } else {
            console.info("商户查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          that.StateManagement.tableData = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '商户查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          that.StateManagement.tableData = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '商户查询失败！', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },

      noStateManagementChange(value) {
        this.value = value
        this.StateManagement.dialogVisible = true
      },
      /**
        * 提交新增商户信息
        * @param //bma_mer_uuid         商家ID
        * @param //bma_from_status    源审核状态明细  2同意；3不同意
        * @param //bma_from_describer 源审核意见
        * @param //bma_to_status      目标审核状态明细  2同意；3不同意
        * @param //bma_to_describer   目标审核意见
        * @param //bma_to_bank_center_id
        * @param //bma_to_bank_province_id
        * @param //bma_to_bank_city_id
        * @param //bma_to_bank_county_id
        * @param //bma_to_bank_station_uuid
      */
      submitForm() {
        this.$confirm('是否进行审核操作?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.StateManagementDate = {
            msg: this.value,
            i: 3
          }
          this.StateManagementChange()
        }).catch(() => {
          return
        });
      },










      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => { });
      },
      /**------分页-------start----*/
      notApprovedSizeChange(val) {
        // console.log(`每页 ${val} 条`);
        this.notApproved.perPageSize = val;
        this.getData();
      },
      notApprovedCurrentChange(val) {
        // console.log(`当前页: ${val}`);
        this.notApproved.currentPage = val;
        this.getData();
      },
      /**------分页-------start----*/
      StateManagementSizeChange(val) {
        // console.log(`每页 ${val} 条`);
        this.StateManagement.perPageSize = val;
        this.StateManagementChange();
      },
      StateManagementCurrentChange(val) {
        // console.log(`当前页: ${val}`);
        this.StateManagement.currentPage = val;
        this.StateManagementChange();
      },
    },
  }
</script>
<style scoped>
  .box>>>.el-table__fixed-right::before,
  .el-table__fixed::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: #EBEEF5;
    z-index: 4;
  }
</style>


