<template>
  <div>
    <div style="display: flex;justify-content: space-between;">
      <div>审核通过列表</div>
    </div>
    <el-table border class="new-table-fixed-right new-table-fixed" ref="multipleTable" :data="adoptApproved.tableData"
      tooltip-effect="dark" :row-style="{'height':'10px'}" :cell-style="{'padding':'5px 0px'}"
      :header-cell-style="headerStyle">
      <el-table-column type="index" label="序号" width="50">
      </el-table-column>
      <el-table-column v-for="(item, index) in adoptApproved.tableMsg" :key="index" :prop="item.key" :label="item.name">
      </el-table-column>
      <el-table-column label="优惠券推广">
        <template slot-scope="scope">
          <i v-if="scope.row.bank_mer_permissions.indexOf('1')>-1" class="el-icon-check"></i>
          <i v-else class="el-icon-close"></i>
        </template>
      </el-table-column>
      <el-table-column label="商品推广">
        <template slot-scope="scope">
          <i v-if="scope.row.bank_mer_permissions.indexOf('2')>-1" class="el-icon-check"></i>
          <i v-else class="el-icon-close"></i>
        </template>
      </el-table-column>

      <el-table-column fixed="right" label="操作" width="400">
        <template slot-scope="scope">
          <!-- <el-button @click="PopupControl(scope,functionMsg)" type="text" size="small">功能开通</el-button> -->
          <el-button @click="PopupControl(scope,serviceChargeMsg,'服务费管理')" type="text" size="small">服务费管理</el-button>
          <el-button @click="PopupControl(scope,merchantInformation,'商户信息')" type="text" size="small">商户信息</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--page start 分页  current-page:当前页，handleSizeChange：每页显示条数改变的时候，page-size：每页显示条数，total：总条数，handleCurrentChange：下一页-->
    <el-pagination style="margin-top: 10px;" background @size-change="adoptApprovedSizeChange"
      @current-change="adoptApprovedCurrentChange" :current-page="adoptApproved.currentPage"
      :page-sizes="[10, 20, 50, 100,200]" :page-size="adoptApproved.perPageSize"
      layout="total, sizes, prev, pager, next, jumper" :total="adoptApproved.total">
    </el-pagination>
    <!-- 功能开通弹框 -->
    <el-dialog title="功能开通" :visible.sync="functionMsg.dialogVisible" width="20%" :before-close="handleClose">
      <div>
        <div style="width: 100%;text-align: left;margin-bottom: 10px;">商户名称：{{functionMsg.name}}</div>
        <div style="margin-bottom: 10px;display: flex;" v-for="(item, index) in functionMsg.msg" :key="index">
          <div style="width: 100px;text-align: left;">{{item.name}}：</div>
          <div>
            <el-radio v-model="item.radio" label="1">开通</el-radio>
            </el-radio>
            <el-radio v-model="item.radio" label="2">关闭</el-radio>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="functionMsg.dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="functionClick()">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 服务费管理弹框 -->
    <el-dialog title="服务费管理" :visible.sync="serviceChargeMsg.dialogVisible" width="80%" :before-close="handleClose">
      <div>
        <div style="width: 100%;text-align: left;margin-bottom: 10px;">商户名称：{{serviceChargeMsg.name}}</div>
        <div style="width: 100%;text-align: left;margin-bottom: 10px;">收款账号：{{serviceChargeMsg.obj.mbmi_mid}}</div>
        <el-table border class="new-table-fixed-right new-table-fixed" ref="multipleTable" :data="addQueryList"
