          <el-table-column type="index" label="序号" width="80">
          </el-table-column>
          <el-table-column prop="mbpb_device_uuid" label="设备编号">
            <template slot-scope="scope" >
              <span>{{scope.row.mbpb_device_uuid| format}}</span>
            </template>
          </el-table-column>
