          ],
          mbmi_mid: [
            { required: true, message: '请输入银联子商户号', trigger: 'blur' },
            // { min: 3, max: 30, message: '长度在 3 到 30 个字符', trigger: 'blur' }
          ],
          bms_work_t1: [
            { required: true, message: '请选择营业时间', trigger: 'change' }
          ],
          bmi_industry_id: [
            { required: true, message: '请选择商户行业', trigger: 'change' }
          ],
          loc: [
            { required: true, message: '请选择地址', trigger: 'change' }
          ]
        },
        //加盟网点信息
        data: [{
          id: 1,
          label: '一级 2',
          children: [{
            id: 3,
            label: '二级 2-1',
            children: [{
              id: 4,
              label: '三级 3-1-1'
            }, {
              id: 5,
              label: '三级 3-1-2',
            }]
          }, {
            id: 2,
            label: '二级 2-2',
            children: [{
              id: 6,
              label: '三级 3-2-1'
            }, {
              id: 7,
              label: '三级 3-2-2',
            }]
          }]
        }],
        // 表格菜单
        tableMsg: [
          { key: 'date', name: '商户名称' },
          { key: 'date', name: '所属行业' },
          { key: 'date', name: '满减券推广' },
          { key: 'date', name: '抽奖优惠券' },
          { key: 'date', name: '商品推广' },
          { key: 'date', name: '代金券推广' }
        ],
        // 功能管理
        functionMsg: {
          name: "xxxxx",
          dialogVisible: false,
          msg: [
            { radio: '1', name: '满减券推广' },
            // { radio: '1', name: '抽奖优惠券' },
            { radio: '1', name: '商品推广' },
            // { radio: '1', name: '代金券推广' }
          ]
        },
        // 服务费管理
        serviceChargeMsg: {
          name: "xxxxxx",
          accountNumber: "xxxxxx",
          dialogVisible: false,
          obj: [],
          msg: [
            { name: '设备', key: 'di_describe' },
            { name: '网点内设备编号', key: 'doi_active_id' },
          ]
        },
        // 商户信息
        merchantInformation: {
          dataMsg: {},
          obj: [],
          name: "xxxxxx",
          accountNumber: "xxxxxx",
          dialogVisible: false,
          value: "",
          value1: [],
          msg: [
            { value: '选项1', label: '黄金糕' },
            { value: '选项2', label: '双皮奶' },
            { value: '选项3', label: '蚵仔煎' },
            { value: '选项4', label: '龙须面' },
            { value: '选项5', label: '北京烤鸭' }
          ],
        },
        // 加盟网点
        franchiseOutlets: {
          name: "xxxxxx",
          dialogVisible: false,
          msg: [
            { value: '中国银行长春分行', label: [{ value: '中国银行长春分行', label: [] }] },
            { value: '选项2', label: '双皮奶' },
            { value: '选项3', label: '蚵仔煎' },
            { value: '选项4', label: '龙须面' },
            { value: '选项5', label: '北京烤鸭' }
          ],
        },

        //表格头样式
        headerStyle: { 'background': '#4B86BC', 'color': '#ffffff', 'borderRight': '1px solid #E3E5EB', 'padding': '0px', 'height': '45px', 'line-height': '45px' },
        //表格数据
        addQueryList: [],
        currentCompanyId: '',
        mid: '',
        tableData: [{
          index: 1,
          date: '2016-05-02',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '上海市普陀区金沙江路 1518 弄',
          zip: 200333
        }]
      }
    },
    created() {
      console.log(this.customGlobal.userInfoExt, "***********************************");
      this.Select_industry()
      this.getData()
      this.bank_commodity()
    },
    methods: {
      //转13位id
      bank_commodity() {
        let that = this;
        let _jsondata_ = {
          companyId: this.customGlobal.userInfoExt.cui_station_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var s1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.bank_transcoding,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        s1.then((res) => {
          if (res.data.status == 'success') {
            loading.close();
            that.currentCompanyId = res.data.currentCompanyId
          } else {
            console.info("转码失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          loading.close();
          this.$alert(JSON.stringify(error.message), '转码出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          this.$alert(JSON.stringify(res.message), '转码出错', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        });
      },

      /**
       * 
      */
      getData() {
        let that = this;
        let _jsondata_ = {
          currentPage: this.adoptApproved.currentPage,
          perPageSize: this.adoptApproved.perPageSize,
          manager_status: 2,
          obj_bank_center_id: this.customGlobal.userInfoExt.cui_bank_center_id,
          obj_province_id: this.customGlobal.userInfoExt.cui_province_id,
          obj_city_id: this.customGlobal.userInfoExt.cui_city_id,
          obj_county_id: this.customGlobal.userInfoExt.cui_county_id,
          obj_station_id: this.customGlobal.userInfoExt.cui_station_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.merchant_examination_passed_query,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          console.log("异步调用成功返回:" + JSON.stringify(res));
          if (res.data.status == 'success') {
            that.adoptApproved.tableData = res.data.rows;
            loading.close();
            if (that.adoptApproved.tableData.length == 0) {
              return
            }
            console.log(res.data.rows[0].bank_mer_permissions, "-***********************************");
            res.data.rows.forEach((element, i) => {
              that.adoptApproved.tableData[i].bank_mer_permissions = element.bank_mer_permissions.map((res1 => {
                return res1.bma_permissions_type
              }))
            })
            console.log(that.adoptApproved.tableData, "--------------**********************");

            that.adoptApproved.total = res.data.total
            loading.close();
          } else {
            that.adoptApproved.tableData = [];
            console.info("商户查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("---------------------------4");

          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          that.adoptApproved.tableData = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '商户查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("---------------------------3");

          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          that.adoptApproved.tableData = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '商户查询失败！', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },

      /**
       * 功能开通
      */
      functionClick() {
        let msg = this.functionMsg.msg.filter(res => {
          return res.radio == 1
        })
        console.log(msg);
        // return
        let that = this;
        let permissions = msg.map(res => {
          return {
            bma_sign_flag: "2",
            bma_permissions_type: res.name == "满减券推广" ? 1 : res.name == "商品推广" ? 2 : 3,
            bma_bank_center_id: this.customGlobal.userInfoExt.cui_bank_center_id,
            bma_province_id: this.customGlobal.userInfoExt.cui_province_id,
            bma_city_id: this.customGlobal.userInfoExt.cui_city_id,
            bma_county_id: this.customGlobal.userInfoExt.cui_county_id,
            bma_station_id: this.customGlobal.userInfoExt.cui_station_id,
          }
        })

        let _jsondata_ = {
          bma_sign_flag: '2',
          bma_mer_uuid: this.functionMsg.obj.mbmi_uuid,
          permissions: permissions,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postManagerMerchant_examination_passed_function_open_update,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          console.log("异步调用成功返回:" + JSON.stringify(res));
          if (res.data.status == 'success') {
            that.getData();
            this.functionMsg.dialogVisible = false
            loading.close();
          } else {
            console.info("商户查询失败");
            loading.close();
            that.getData();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("---------------------------4");

          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          that.getData();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '商户查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("---------------------------3");

          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          that.getData();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '商户查询失败！', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
