<template>
  <div>
    <!-- <el-tabs style="margin-left: 20px;width:98%" v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="待审核" name="first">
        <toBeReviewed style="margin-top: 10px;" />
      </el-tab-pane>
      <el-tab-pane label="审核通过" name="second">
        <Approved style="margin-top: 10px;" />
      </el-tab-pane> -->
    <!-- <el-tab-pane label="已停用" name="third">已停用</el-tab-pane> -->
    <!-- </el-tabs> -->
    <el-menu :default-active="activeIndex" :style="{ height: '60px'}" class="el-menu-demo" mode="horizontal"
      @select="handleSelect">
      <el-menu-item index="1" style="margin: 0 10px">待审核</el-menu-item>
      <el-menu-item index="2" style="margin: 0 10px">审核通过</el-menu-item>
    </el-menu>
    <div style="width: 98%;margin: 0 auto;">
      <toBeReviewed ref="toBeReviewedClick" v-show="activeIndex==1" style="margin-top: 10px;" />
      <Approved ref="ApprovedClick" v-show="activeIndex==2" style="margin-top: 10px;" />
    </div>
  </div>
</template>
<script>
  import toBeReviewed from "./component/toBeReviewed.vue"
  import Approved from "./component/Approved.vue"
  export default {
    data() {
      return {
        activeName: 'first',
        activeIndex: '1',
      };
    },
    components: { toBeReviewed, Approved },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      },
      handleSelect(key, keyPath) {
        this.activeIndex = key
        switch (key) {
          case '1':
            this.$refs.toBeReviewedClick.getData();
            break;
          case '2':
            this.$refs.ApprovedClick.getData();
            this.$refs.ApprovedClick.Select_industry();
            this.$refs.ApprovedClick.bank_commodity();
            break;
        }
      }
    }
  };
</script>


